import { Trans } from '@lingui/macro'
import { useObserver } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components/macro'

import { useRoot } from '../../app/useRoot'
import { AlertModal } from '../../common/AlertModal'
import Pacman from '../../common/Pacman'

const TextPadding = styled.div`
  padding-bottom: 2rem;
`

export const OrderGoPayModal: React.FC = () => {
  const { order: orderModel } = useRoot()
  return useObserver(() => (
    <AlertModal isOpen={orderModel.isGoPayChecking} headerText="GoPay platba">
      <TextPadding>
        <Trans>Checking your GoPay payment, please wait...</Trans>
      </TextPadding>
      <Pacman noMargin={true} />
    </AlertModal>
  ))
}
