import { Trans } from '@lingui/macro'
import { useObserver } from 'mobx-react-lite'
import React from 'react'
import { Alert, Button } from 'reactstrap'
import styled from 'styled-components/macro'

import { useRoot } from '../../app/useRoot'
import { mediaDesktop, mediaMobile } from '../../common/Responsive'

interface IProps {}

const NoticeButton = styled(Button)`
  font-size: 0.9rem;
  margin: 0.2rem 1rem;
  padding: 0.5em 1rem;
`

const StorageNotice = styled(Alert)`
  width: 100%;
  z-index: 5;
  ${mediaDesktop`
    font-size: 0.9rem;
    position: sticky;
    bottom: 0;
  `};
  ${mediaMobile`
    position: fixed;
    top: 5rem;
    font-size: 0.8rem;
    `};
  padding: 0.5rem 1rem;
  background-color: #d4eddadc;
`

export const OrderFormStorageNotice: React.FC<IProps> = () => {
  const { user } = useRoot()

  return useObserver(() => {
    if (user.rememberForm !== null) {
      return null
    }
    return (
      <StorageNotice
        data-testid="OrderFormStorageNotice"
        className="animated slideInUp"
      >
        <p>
          <Trans>
            This app is using secure storage for keeping filled form data. It's
            not cookies, these are stored in your browser only.
          </Trans>
        </p>

        <NoticeButton
          color="success"
          size="sm"
          onClick={user.setFormRememberYes}
          data-testid="orderFormStorageNotice__Yes"
        >
          <Trans>Always remember</Trans>
        </NoticeButton>
        <NoticeButton
          color="secondary"
          size="sm"
          onClick={user.setFormRememberNo}
          data-testid="orderFormStorageNotice__No"
        >
          <Trans>Do not store</Trans>
        </NoticeButton>
      </StorageNotice>
    )
  })
}
