import { useAutorun, useReaction } from '@speedlo/hooks'
import { useAsObservableSource } from 'mobx-react-lite'

import { useRoot } from '../../../app/useRoot'
import { useQInitialDeliveryLocation } from '../../../graph/generated'
import { useMapPanning } from './useMapPanning'

export const useDeliveryMapPanning = () => {
  const { location } = useRoot()
  const { data, loading } = useQInitialDeliveryLocation(
    {},
    {
      fetchPolicy: 'cache-first',
      skip: location.hasAddress,
    },
  )

  const source = useAsObservableSource({
    initial: loading ? null : data.location,
  })

  const panToGps = useMapPanning()

  useAutorun(() => {
    if (location.geoPoint) {
      location.log('pan to user location', location.geoPoint)
      panToGps(location.geoPoint)
    } else if (location.geolocationPoint) {
      location.log('pan to geolocation', location.geolocationPoint)
      panToGps(location.geolocationPoint)
    } else if (source.initial && source.initial.gpsCoordinates) {
      location.log('pan to initial location', source.initial.gpsCoordinates)
      panToGps(source.initial.gpsCoordinates)
    }
  })

  useReaction(
    () => location.geolocationPoint,
    gps => {
      if (gps) {
        panToGps(gps)
      }
    },
  )
}
