import { useReaction } from '@speedlo/hooks'
import { debounce } from 'debounce'
import { reaction } from 'mobx'
import { useLocalStore } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../../app/useRoot'

interface IAddressSearchOptions {
  count?: number
  watchLocation?: boolean
}

export type AddressSearchResult = ReturnType<typeof mapResult>

const urlCache = new Map()

export function useAddressSearch({
  count = 1,
  watchLocation = false,
}: IAddressSearchOptions = {}) {
  const { map, location } = useRoot()

  const state = useLocalStore(() => ({
    results: null as RoA<AddressSearchResult> | null,
    phrase: location.searchValue,
    searching: false,
    get url() {
      const params = [
        `count=${count}`,
        `lon=${map.longitude}`,
        `lat=${map.latitude}`,
        `zoom=${map.zoom}`,
        `lang=cs,sk,en`,
        `enableCategories=1`,
      ]
      return `https://api.mapy.cz/suggest/?${params.join('&')}`
    },
  }))

  const doFetch = React.useCallback(() => {
    if (state.phrase.length === 0) {
      state.results = null
      return
    }

    location.log(`searching phrase: ${state.phrase}`)
    const url = `${state.url}&phrase=${state.phrase}`

    if (urlCache.has(url)) {
      state.results = urlCache.get(url)
      return
    }

    state.searching = true

    fetch(url)
      .then(resp => resp.json())
      .then(json => {
        const results = json.result.filter(validateResult).map(mapResult)
        urlCache.set(url, results)
        state.results = results
      })
      .finally(() => {
        state.searching = false
      })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [location])

  const debouncedFetch = React.useRef(debounce(doFetch, 500))

  const onSearch = React.useCallback(
    (nextPhrase: string = location.searchValue) => {
      location.log(`onSearch ${nextPhrase}`)
      state.phrase = nextPhrase.trim()
      debouncedFetch.current.clear()
      doFetch()
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [doFetch, location],
  )

  useReaction(
    () => location.searchValue,
    value => {
      if (value.length === 0) {
        state.phrase = ''
        state.results = null
        debouncedFetch.current.clear()
      }
    },
  )

  React.useEffect(
    () =>
      reaction(
        () => state.url,
        () => {
          if (watchLocation) {
            debouncedFetch.current()
          }
        },
      ),
    [state.url, watchLocation],
  )

  return { ...state, onSearch }
}

function validateResult({ userData }) {
  const { latitude, longitude } = userData
  return latitude && longitude
}

function mapResult({ category, userData }) {
  const {
    id,
    latitude,
    longitude,
    suggestFirstRow,
    suggestSecondRow,
    municipality,
    district,
    country,
  } = userData

  const suffix =
    [municipality, district, country, suggestSecondRow].find(Boolean) || ''

  return {
    id,
    category,
    gps: {
      latitude,
      longitude,
    },
    address: `${suggestFirstRow}${suffix ? `, ${suffix}` : ''}`,
  }
}
