import { Trans } from '@lingui/macro'
import React from 'react'
import { Link } from 'react-router-dom'
import { Button } from 'reactstrap'
import styled from 'styled-components/macro'

import { PrimaryButton } from '../../common/Buttons'
import { Desktop, Mobile } from '../../common/Responsive'
import { QOrderFinishQuery } from '../../graph/generated'

interface IProps {
  finish: QOrderFinishQuery
}
const CenteredButtonContainer = styled.div`
  text-align: center;
`

export const FinishViewFooter: React.FC<IProps> = ({
  finish: { androidApp, iosApp },
}) => {
  const renderHomeButton = () => (
    <Link to="/">
      <PrimaryButton className="finish__view__submit__line__button">
        <Trans>Back to home page</Trans>
      </PrimaryButton>
    </Link>
  )
  return (
    <>
      <Desktop>
        {(androidApp || iosApp) && (
          <div className="finish__view__apps__propagation">
            <div className="finish__view__apps__propagation__header">
              <Trans>What about mobile app?</Trans>
            </div>
            <div className="finish__view__apps__propagation__description">
              <Trans>
                We have prepared mobile app, so you could order from anywhere!
              </Trans>
            </div>

            {androidApp && (
              <a href={androidApp.url}>
                <Button
                  className="finish__view__apps__propagation__button"
                  color="secondary"
                >
                  Google Play
                </Button>
              </a>
            )}

            {iosApp && (
              <a href={iosApp.url}>
                <Button
                  className="finish__view__apps__propagation__button"
                  color="secondary"
                >
                  App Store
                </Button>
              </a>
            )}
          </div>
        )}
        <CenteredButtonContainer>{renderHomeButton()}</CenteredButtonContainer>
      </Desktop>

      <Mobile>
        <div
          className="finish__view__submit__line"
          style={{ textAlign: 'center' }}
        >
          <div className="finish__view__submit__line__strip" />
          {renderHomeButton()}
        </div>
      </Mobile>
    </>
  )
}
