import { isoDate } from '@speedlo/graphql'
import { RM } from '@speedlo/tools'
import {
  addMinutes,
  endOfMinute,
  getMinutes,
  startOfMinute,
  subMinutes,
  toDate,
} from 'date-fns'
import React from 'react'

import { useRoot } from '../app/useRoot'
import {
  QDelayedOrderDelayedOrder,
  QDelayedOrderIntervals,
  useMOrderDeliveryTime,
  useQDelayedOrder,
} from '../graph/generated'
import { useLoadingCache } from './useLoadingCache'

const STEP_SIZE_MINUTES = 15

export function useDelayedOrder() {
  const { order, cart } = useRoot()

  const skip = !order.branchId

  const getData = useLoadingCache<Maybe<QDelayedOrderDelayedOrder>>(skip)

  const { data, loading } = useQDelayedOrder(
    { branchId: order.branchId! },
    { skip },
  )

  const cachedData = getData(loading, () => {
    return skip ? null : data.delayedOrder
  })

  const getSteps = React.useCallback(() => {
    if (cachedData === null) {
      return null
    }
    return createSteps(cachedData.intervals)
  }, [cachedData])

  const canBeDelayed = React.useCallback(() => {
    return cachedData !== null && cachedData.intervals.length > 0
  }, [cachedData])

  const [updateOrder] = useMOrderDeliveryTime()

  const changeDeliveryTime = React.useCallback(
    (deliverAt: Maybe<Date>) => {
      updateOrder({
        orderId: order.orderId!,
        deliverAt: deliverAt && isoDate(deliverAt),
        withGoods: cart.withGoods,
      })
      order.setDeliverAt(deliverAt)
    },
    [cart, order, updateOrder],
  )

  return {
    getSteps,
    canBeDelayed,
    isEnabled: Boolean(cachedData) && cachedData!.enabled,
    changeDeliveryTime,
  }
}

function createSteps(intervals: RoA<QDelayedOrderIntervals>) {
  return RM.flatten(intervals.map(createTimeSteps))
}

function createTimeSteps({ start, end }: QDelayedOrderIntervals) {
  let current = startOfMinute(timeClampUp(toDate(start), STEP_SIZE_MINUTES))
  const steps: Date[] = []
  const endMinute = endOfMinute(subMinutes(toDate(end), 1))
  do {
    steps.push(current)
    current = addMinutes(current, STEP_SIZE_MINUTES)
  } while (current <= endMinute)
  return steps
}

function timeClampUp(time: Date, minuteStep: number) {
  return addMinutes(timeClampDown(time, minuteStep), minuteStep)
}

function timeClampDown(time: Date, minuteStep: number) {
  return subMinutes(time, getMinutes(time) % minuteStep)
}
