import { cn } from '@speedlo/ui'
import { useXField } from '@speedlo/xform'
import { useObserver } from 'mobx-react-lite'
import React from 'react'
import { Input } from 'reactstrap'
import styled from 'styled-components/macro'

interface IProps extends React.InputHTMLAttributes<HTMLInputElement> {
  name: string
  children: ReactNode
}

export const StyledFormCheckbox = styled.div`
  display: flex;
  margin-bottom: 0.5rem;

  input {
    -webkit-appearance: none;
    -moz-appearance: none;
    background-color: white;
    border: 1px solid;
    border-color: #c5c5c5;
    width: 2rem;
    height: 2rem;
    min-width: 2rem;
    border-radius: 0.25rem;
    position: relative;
    margin: 0;
    outline: none;
    &:checked {
      background-color: white;
    }
    &:checked:after {
      content: '\\2714';
      font-size: 2rem;
      position: absolute;
      top: -0.5rem;
      left: 0.1rem;
      color: #1d1d1d;
      overflow: hidden;
    }
    &.is-invalid {
      border-color: red;
    }
  }
  label {
    margin: 0 0 0 0.8rem;
    vertical-align: top;
  }
`

export const FormCheckbox: React.FC<IProps> = ({
  name,
  children,
  ...element
}) => {
  const field = useXField(name)
  return useObserver(() => (
    <StyledFormCheckbox
      className={cn({ 'is-invalid': field.touched && Boolean(field.error) })}
    >
      <Input
        {...element}
        id={field.name}
        name={field.name}
        checked={field.value === true}
        onChange={() => field.setValue(!field.value)}
        type="checkbox"
      />
      <label htmlFor={name}>{children}</label>
    </StyledFormCheckbox>
  ))
}
