import React from 'react'
import { ButtonProps } from 'reactstrap'
import styled from 'styled-components/macro'

import { PickerButton } from '../../common/PickerButton'
import { FRecipeDetailSideDishes } from '../../graph/generated'
import { SidedishRemoveButton } from './SidedishRemoveButton'

interface IProps extends ButtonProps {
  sidedish: FRecipeDetailSideDishes
  count: number
  isSelected: boolean
  canBeSelected: boolean
  canBeRemoved: boolean
  onAdd(): void
  onRemove(): void
}

const StyledSidedishCheckbox = styled.div`
  display: inline-block;
  position: relative;

  & .name {
    float: left;
    text-align: left;
    white-space: normal;
    max-width: 13rem;
  }

  & .price {
    font-weight: bold;
    float: right;
  }

  & .count {
    display: inline-block;
    margin-left: 0.5rem;
  }
`

export const SidedishCheckbox = React.memo<IProps>(
  ({
    sidedish,
    count,
    canBeSelected,
    canBeRemoved,
    className,
    onAdd,
    onRemove,
    ...buttonProps
  }) => {
    const onClick = React.useCallback(() => {
      if (canBeSelected) {
        onAdd()
      }
    }, [canBeSelected, onAdd])
    return (
      <StyledSidedishCheckbox className={className}>
        <PickerButton
          onClick={onClick}
          disabled={!canBeSelected}
          size="xs"
          outline={true}
          width="17.5rem"
          data-testid={`sidedishCheckbox__${sidedish.id}`}
          {...buttonProps}
        >
          <div className="name">
            {sidedish.nameLabel}
            <span key={count} className="animated heartBeat count">
              {count > 1 && `${count}x`}
            </span>
          </div>
          <div className="price">{sidedish.price.formattedValue}</div>
        </PickerButton>
        {canBeRemoved && <SidedishRemoveButton onClick={onRemove} />}
      </StyledSidedishCheckbox>
    )
  },
)
