import { observer } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../app/useRoot'
import { SelectButtonThemed } from '../../common/Buttons'
import { PaymentGateEnum } from '../../graph/types'

export interface IPaymentButtonProps {
  paymentMethod: PaymentGateEnum
}

export const OrderPaymentButton = observer<IPaymentButtonProps>(
  ({ children, paymentMethod }) => {
    const { order: orderModel } = useRoot()
    const isSelected = orderModel.paymentMethod === paymentMethod
    return (
      <SelectButtonThemed
        disabled={orderModel.disableInteraction}
        selected={isSelected}
        onClick={() => {
          orderModel.setPaymentMethod(paymentMethod)
        }}
        data-testid={`orderPaymentMethod-${paymentMethod}`}
      >
        <Checkmark show={isSelected} />
        {children}
      </SelectButtonThemed>
    )
  },
)

function Checkmark({ show }: { show: boolean }) {
  return show ? (
    <span className="flaticon-mark order__payment__method__button__checked" />
  ) : (
    <div className="order__payment__method__button__unchecked" />
  )
}
