import { DateFormat } from '@lingui/macro'
import { useNow } from '@speedlo/hooks'
import { DF, RM } from '@speedlo/tools'
import { isAfter, startOfDay } from 'date-fns/esm/fp'
import React, { Fragment } from 'react'
import styled from 'styled-components/macro'

import { TimePickerButton } from './TimePickerButton'

interface IProps {
  steps: RoA<Date>
  initialTime?: Maybe<Date>
  onStepChange(step: Date): void
}

const TimePickerContainer = styled.div`
  margin-top: 1rem;
`

const TimePickerDay = styled.div`
  font-size: 1rem;
  font-weight: bold;
  text-align: left;
  margin: 0 0 0.2rem 0.2rem;
`

export const TimePicker: React.FC<IProps> = ({
  steps: rawSteps,
  initialTime,
  onStepChange,
}) => {
  const getNow = useNow()

  const steps = React.useMemo(() => {
    const filtered = rawSteps.filter(isAfter(getNow()))
    return RM.sort(RM.ascend(RM.invoker(0, 'getTime')), filtered)
  }, [getNow, rawSteps])

  const days = React.useMemo(() => {
    return RM.uniq(RM.map(startOfDay, steps)) as Date[]
  }, [steps])

  const [stepIdx, setStepIdx] = React.useState(0)

  React.useEffect(() => {
    if (initialTime) {
      setStepIdx(DF.closestIndexTo(initialTime, steps))
    }
  }, [initialTime, steps])

  return (
    <TimePickerContainer>
      {days.map(day => (
        <Fragment key={day.getDate()}>
          <TimePickerDay>
            <DateFormat
              value={day}
              format={{ month: 'long', day: 'numeric', weekday: 'long' }}
            />
          </TimePickerDay>
          {steps.map((step, idx) =>
            DF.isEqual(day, DF.startOfDay(step)) ? (
              <TimePickerButton
                key={step.getTime()}
                time={step}
                isSelected={idx === stepIdx}
                canBeSelected={true}
                onClick={() => {
                  setStepIdx(idx)
                  onStepChange(step)
                }}
              />
            ) : null,
          )}
        </Fragment>
      ))}
    </TimePickerContainer>
  )
}
