import { OverlayView } from '@react-google-maps/api'
import React from 'react'
import styled from 'styled-components/macro'

import { FPickupBranchFragment } from '../graph/generated'
import { gpsToLatLng } from '../helpers/gps'

interface IProps {
  branch: FPickupBranchFragment
  onClick?: () => void
}

const StyledPickupMapMarker = styled.div`
  cursor: pointer;
  color: white;
  opacity: 0.5;

  &::before {
    font-size: 1.3rem;
    margin: 0;
    padding: 0.5rem;
    background-color: #ff0000;
    border-radius: 1rem;
  }
`

export const MapBranchMarker: React.FC<IProps> = ({ branch, onClick }) => {
  return (
    <OverlayView
      position={gpsToLatLng(branch.gps)}
      mapPaneName={OverlayView.OVERLAY_MOUSE_TARGET}
      getPixelPositionOffset={(width, height) => ({
        x: width === 0 ? -12 : -(width / 2),
        y: height === 0 ? -12 : -(height / 2),
      })}
    >
      <StyledPickupMapMarker
        className="flaticon-restaurant"
        data-testid="PickupMapMarker"
        title={branch.name}
        onClick={onClick}
      />
    </OverlayView>
  )
}
