import { observer } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../../app/useRoot'
import { useAlertModal } from '../../../hooks/useAlertModal'
import { i18n } from '../../../i18n'

export const GeolocationButton = observer(() => {
  const { location } = useRoot()

  const [alert, makeAlert] = useAlertModal()

  if (!location.isGeolocationAvailable || location.isResolving) {
    return null
  }

  const attemptGeolocation = () => {
    location.log('geolocating...')
    navigator.geolocation.getCurrentPosition(
      ({ coords }) => {
        const { latitude, longitude } = coords
        location.log(
          'geolocated at coords %s, %s (acc: %s)',
          coords.latitude,
          coords.longitude,
          coords.accuracy,
        )
        location.setGeolocationPoint({ latitude, longitude })
      },
      err => {
        location.log('failed geolocation', err)
        location.disableGeolocation()
        makeAlert(
          i18n.t`Geolocation failed`,
          i18n.t`Your browser does not support geolocation.`,
        )
      },
      {
        enableHighAccuracy: true,
        maximumAge: 60 * 1000,
      },
    )
  }

  return (
    <>
      {alert}
      <div className="gps" onClick={attemptGeolocation}>
        <span className="flaticon-gps" />
      </div>
    </>
  )
})

export default GeolocationButton
