import { observable, ObservableMap, runInAction } from 'mobx'
import React from 'react'

import { useQFoodMenuCartPresence } from '../../../graph/generated'

export function useFoodMenuPresence(orderId: MaybeID) {
  const { data, loading } = useQFoodMenuCartPresence(
    {
      orderId: orderId!,
    },
    {
      skip: !orderId,
      fetchPolicy: 'cache-only',
    },
  )

  const [presence] = React.useState(() => observable.map<ID, number>())

  if (!loading) {
    runInAction(() => {
      presence.clear()
      data.order.presence.forEach(({ recipe: { id } }) => {
        if (!presence.has(id)) {
          presence.set(id, 0)
        }
        presence.set(id, presence.get(id)! + 1)
      })
    })
  }

  return presence
}

export type TFoodMenuPresence = ObservableMap<ID, number>
