import { OrderStateCategoryEnum, useQOrderForm } from '../../../graph/generated'
import { useOrderModel } from '../../../hooks/useOrderModel'

export function useOrderFormCheck() {
  const orderModel = useOrderModel()

  const { data, loading } = useQOrderForm(
    { orderId: orderModel.orderId! },
    { skip: !orderModel.orderId },
  )

  const isOrderInCart =
    loading || data.order.orderStateCategory === OrderStateCategoryEnum.InCart
  const hasItemsInCart = !loading && data.order.countWares > 0

  return { isOrderInCart, hasItemsInCart }
}
