import './FoodMenuItem.css'

import { cn } from '@speedlo/ui'
import { useObserver } from 'mobx-react-lite'
import React from 'react'
import Truncate from 'react-truncate'
import styled from 'styled-components/macro'

import { mediaMobile } from '../../common/Responsive'
import {
  FFoodMenuRecipeFragment,
  useExecutableQCartRecipe,
} from '../../graph/generated'
import { useOrderModel } from '../../hooks/useOrderModel'
import { TFoodMenuPresence } from './hooks/useFoodMenuPresence'

interface IProps {
  recipe: FFoodMenuRecipeFragment
  presence: TFoodMenuPresence
  onAddToCart(recipeId: ID, hasSideDish: boolean): void
}

const StyledFoodMenuItem = styled.div`
  padding: 1rem 0.5rem 0.3rem 1.1rem;
  border: 1px solid #d5d5d5 !important;
  margin: 0 0 0.4rem 0;
  border-radius: 0.8rem;
  position: relative;
  min-height: 6rem;
  user-select: none;
  &:hover:not(.disabled) {
    cursor: pointer;
    background-color: #c9e8d7;
  }
  &.active {
    background-color: #b3ebcc;
  }
  h4 {
    font-size: 1rem;
    font-weight: 700;
    color: #404040;
    margin-bottom: 0.1rem;
    margin-right: 6rem;
  }
  p {
    color: #666666;
    margin: 0.6rem 0 0 0;
    font-size: 0.9rem;
    max-height: 2.5rem;
    -webkit-line-clamp: 2;
    overflow: hidden;
  }

  & div.presence {
    background-color: ${({ theme }) => theme.secondary};
    float: left;
    position: absolute;
    left: -1.2rem;
    padding: 0.4rem 0.6rem;
    color: white;
    border-radius: 1.8rem;
    font-size: 0.9rem;
    ${mediaMobile`
    left: -0.6rem;
    top: -0.7rem;
  `};
  }
`
const FoodMenuItemImage = styled.img`
  width: 6rem;
  height: 6rem;
  display: block;
  float: left;
  margin: -1.05rem 1rem 0 -1.2rem;
  border-radius: 0.8rem 0 0 0.8rem;
  object-fit: cover;
`

const FoodMenuItemPriceCorner = styled.div`
  border-top-width: 1.8rem;
  border-top-style: solid;
  border-top-color: ${({ theme }) => theme.primary} !important;
  position: absolute;
  right: 4.799rem;
  top: 1rem;
  width: 0;
  height: 0;
  border-left: 1.8rem solid transparent;
`

const FoodMenuItemPrice = styled.div`
  background-color: ${({ theme }) => theme.primary} !important;
  position: absolute;
  right: 0;
  top: 1rem;
  width: 4.8rem;
  height: 1.8rem;
  padding: 0.12rem 0.7rem 0.3rem 0rem;
  font-weight: 500;
  font-size: 1.1rem;
  color: white;
  text-align: right;
  overflow: hidden;
  white-space: nowrap;
`

const FoodMenuItem = React.memo<IProps>(({ recipe, presence, onAddToCart }) => {
  const orderModel = useOrderModel()

  const hasSideDishes = recipe.sideDishCount > 0

  const onClick = React.useCallback(() => {
    onAddToCart(recipe.id, hasSideDishes)
  }, [hasSideDishes, onAddToCart, recipe.id])

  const [prefetchFoodItem] = useExecutableQCartRecipe({
    fetchPolicy: 'cache-first',
  })

  const onMouseEnter = React.useCallback(() => {
    if (process.env.NODE_ENV !== 'development' && hasSideDishes) {
      prefetchFoodItem({ recipeId: recipe.id })
    }
  }, [hasSideDishes, prefetchFoodItem, recipe.id])

  const previousPresence = React.useRef(-1)

  const renderPresence = () => {
    const recipePresence = presence.get(recipe.id)
    if (!recipePresence) {
      previousPresence.current = -1
      return null
    }
    const className = cn('presence', 'animated', {
      pulse: recipePresence > 1 || previousPresence.current > 0,
      zoomIn:
        (recipePresence === 1 && previousPresence.current === 0) ||
        previousPresence.current === -1,
    })
    previousPresence.current = recipePresence
    return (
      <div
        key={recipePresence}
        className={className}
      >{`${recipePresence}x`}</div>
    )
  }

  return useObserver(() => (
    <StyledFoodMenuItem
      onClick={onClick}
      onMouseEnter={onMouseEnter}
      className={cn({
        active: orderModel.lastActiveRecipeId === recipe.id,
        disabled: orderModel.disableInteraction,
      })}
      data-testid={`foodMenuItem__${recipe.id}`}
    >
      {recipe.image && (
        <FoodMenuItemImage src={recipe.image} alt={recipe.nameLabel} />
      )}

      <FoodMenuItemPriceCorner />
      <FoodMenuItemPrice data-testid={`foodMenuItem__Price__${recipe.id}`}>
        {recipe.price.formattedValue}
      </FoodMenuItemPrice>
      {renderPresence()}
      <h4 title={recipe.nameLabel}>
        <Truncate lines={2} data-testid={`foodMenuItem__Name__${recipe.id}`}>
          {recipe.nameLabel}
        </Truncate>
      </h4>
      <p>
        <Truncate
          lines={2}
          data-testid={`foodMenuItem__Description__${recipe.id}`}
        >
          {recipe.descriptionLabel}
        </Truncate>
      </p>
    </StyledFoodMenuItem>
  ))
})

export default FoodMenuItem
