import { Trans } from '@lingui/macro'
import { observer } from 'mobx-react-lite'
import React from 'react'
import { Alert } from 'reactstrap'
import styled from 'styled-components/macro'

import { useRoot } from '../../app/useRoot'
import { mediaDesktop } from '../../common/Responsive'
import { gpsToLatLngLit } from '../../helpers/gps'
import { i18n } from '../../i18n'
import { AddressSearchInput } from './destination/AddressSearchInput'
import GeolocationButton from './destination/GeolocationButton'
import { ResolvedAddress } from './destination/ResolvedAddress'
import { ExtraAddressResult } from './ExtraAddressResult'
import { useAddressSearch } from './hooks/useAddressSearch'

const Styled = styled.div`
  width: 90%;
  min-width: 14rem;
  margin: 1rem auto;
  text-align: center;

  div.container {
    display: flex;
    border-radius: 1rem;
    border: 1px solid #d4d4d4;
    background-color: white;
    ${mediaDesktop`
    height: 2.3rem;
    `}
  input {
    height: auto;
    background: transparent;
    border: none;
    &:focus {
      box-shadow: initial;
    }
    &.hidden {
      display: none;
    }
  }

  div.gps {
    color: ${({ theme }) => theme.secondary};
    cursor: pointer;
    position: relative;
    top: 0.2rem;
  }

  div.resolved__address {
    margin: 0.4rem 0.7rem 0.4rem 0.7rem;
    font-size: 1rem;
    color: green;
    width: 100%;
    cursor: pointer;
    &.hidden {
      display: none;
    }
    &.wait {
      display: flex;
      justify-content: center;
      font-size: 0.5rem;
      cursor: wait;
    }
  }

  .alert {
    pointer-events: none;
    margin-bottom: 0;
  }
`

export const DeliveryAddressSearch = observer(() => {
  const { map, location } = useRoot()

  const { results, searching, onSearch } = useAddressSearch()

  React.useEffect(() => {
    if (searching) {
      location.setIsResolving(searching)
    }
  }, [location, searching])

  React.useEffect(() => {
    if (results === null) {
      return
    }

    if (results.length === 0) {
      location.log('no result from direct geocoder api')
      location.setAsNotResolved()
      return
    }

    const [singleResult] = results
    const {
      category,
      gps: { latitude, longitude },
    } = singleResult

    if (map.mapRef) {
      map.log(`panning to result gps ${latitude} / ${longitude}`)
      map.mapRef.panTo(gpsToLatLngLit(singleResult.gps))
    }

    if (!(category && category.startsWith('address'))) {
      location.log('result with category %s', category)
      location.setAsNotAddress()
      return
    }

    location.log(`found address ${singleResult.address}`)
    if (location.latitude !== latitude && location.longitude !== longitude) {
      location.resetResolution()
    }
    location.setSearchResult(singleResult)
  }, [location, map, results])

  return (
    <Styled>
      <div className="container">
        <ResolvedAddress
          address={location.address || ''}
          isVisible={location.hasAddress}
          isResolving={location.isResolving}
          onValidClick={location.clearAddress}
        />
        <AddressSearchInput
          inputProps={{
            placeholder: i18n.t`Type address or choose it from map`,
            autoComplete: 'shipping street-address',
          }}
          onSearch={onSearch}
          isVisible={!(location.hasAddress || location.isResolving)}
        />
        <GeolocationButton />
      </div>
      <ErrorMessage />
      <ExtraAddressResult />
    </Styled>
  )
})

function ErrorMessage() {
  const { location } = useRoot()
  if (!location.hasResolutionResult) {
    return null
  }
  if (location.isResolved === false) {
    return (
      <Alert color="danger">
        <Trans>Address was not recognized.</Trans>
      </Alert>
    )
  }
  if (location.isStreetAddress === false) {
    return (
      <Alert color="warning">
        <Trans>Please enter address with house number.</Trans>
      </Alert>
    )
  }
  if (location.inDeliveryLocation === false) {
    return (
      <Alert color="warning">
        <Trans>
          Meal can't be delivered to your chosen address by any restaurant.
        </Trans>
      </Alert>
    )
  }
  return null
}
