import { DateFormat, Trans } from '@lingui/react'
import { useObserver } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components/macro'

import { SelectButtonThemed } from '../../common/Buttons'
import { useDelayedOrder } from '../../hooks/useDelayedOrder'
import { useOrderModel } from '../../hooks/useOrderModel'

const StyledOrderTimeButtons = styled.div`
  margin: 0 0 1.4rem 0;
  button {
    position: relative;
    padding-top: 0.3rem;
    margin-bottom: 0.2rem;
    height: 2.8rem;
  }
  button:first-of-type {
    margin-right: 0.8rem;
  }
`

export const OrderTimeButtons = () => {
  const orderModel = useOrderModel()

  const { isEnabled, changeDeliveryTime } = useDelayedOrder()

  return useObserver(() => {
    if (!isEnabled) {
      return null
    }
    return (
      <StyledOrderTimeButtons>
        <SelectButtonThemed
          data-testid="orderTimeButtonNow"
          selected={!orderModel.isDelayed}
          disabled={orderModel.disableInteraction}
          onClick={() => changeDeliveryTime(null)}
        >
          <Trans>When possible</Trans>
        </SelectButtonThemed>
        <SelectButtonThemed
          data-testid="orderTimeButtonLater"
          selected={orderModel.isDelayed}
          disabled={orderModel.disableInteraction}
          onClick={orderModel.openTimePicker}
        >
          {orderModel.isDelayed ? (
            <DateFormat
              value={orderModel.deliverAt}
              format={{ weekday: 'long', hour: 'numeric', minute: 'numeric' }}
            />
          ) : (
            <Trans>Choose time</Trans>
          )}
        </SelectButtonThemed>
      </StyledOrderTimeButtons>
    )
  })
}
