import { useAutorun } from '@speedlo/hooks'
import { useXFieldState } from '@speedlo/xform'
import React from 'react'
import { FormGroup, InputGroup } from 'reactstrap'
import { UAParser } from 'ua-parser-js'

import { FormTextInput } from '../../forms/FormTextInput'
import { useOrderModel } from '../../hooks/useOrderModel'
import { i18n } from '../../i18n'

export const OrderPhoneInput: React.FC = () => {
  const agent = new UAParser(window.navigator.userAgent)
  const isMobileSafari = agent.getBrowser().name === 'Mobile Safari'

  const orderModel = useOrderModel()
  const prefixField = useXFieldState<string>('phonePrefix')

  useAutorun(() => {
    const prefix = orderModel.defaultPhonePrefix
    if (
      prefix &&
      orderModel.root.user.memoryApplied &&
      prefixField.value.length === 0
    ) {
      prefixField.setValue(prefix)
      if (prefix) {
        prefixField.setTouched()
      }
    }
  })

  return (
    <FormGroup>
      <InputGroup>
        <FormTextInput
          name="phonePrefix"
          autoComplete={isMobileSafari ? 'off' : 'tel-country-code'}
          placeholder="Prefix"
        />
        <FormTextInput
          name="phone"
          type="tel"
          autoComplete="tel-national"
          placeholder={i18n.t`Phone`}
          style={{ width: '55%' }}
        />
      </InputGroup>
    </FormGroup>
  )
}
