import { Trans } from '@lingui/macro'
import { useObserver } from 'mobx-react-lite'
import React from 'react'
import { Link } from 'react-router-dom'
import { Alert } from 'reactstrap'
import styled from 'styled-components'

import { SecondaryButton } from '../../common/Buttons'
import { useOrderModel } from '../../hooks/useOrderModel'

interface IProps {}

const StyledOrderLocked = styled(Alert)`
  font-weight: bold;
  margin-bottom: 0;
  padding: 0.5rem 1rem;

  & button {
    margin: 0.5rem;
  }
`

export const OrderLocked: React.FC<IProps> = () => {
  const orderModel = useOrderModel()
  return useObserver(() => {
    if (!orderModel.disableInteraction) {
      return null
    }

    return (
      <StyledOrderLocked color="warning">
        <Trans>Order is locked for further changes.</Trans>
        <Link to="/">
          <SecondaryButton>
            <Trans>Create new order</Trans>
          </SecondaryButton>
        </Link>
      </StyledOrderLocked>
    )
  })
}
