import React from 'react'

export function useLoadingCache<TValue>(shouldSkip: boolean = false) {
  const cached = React.useRef<TValue | null>(null)

  // clear cache when skipping
  React.useEffect(() => {
    if (shouldSkip) {
      cached.current = null
    }
  }, [shouldSkip])

  const buildValue = React.useCallback(
    (loading: boolean, getNext: () => TValue) => {
      if (loading && !shouldSkip) {
        return cached.current
      } else {
        const next = getNext()
        cached.current = next
        return next
      }
    },
    [shouldSkip],
  )

  return buildValue
}
