import { Trans } from '@lingui/macro'
import { useAutorun } from '@speedlo/hooks'
import { useVamAgreements } from '@speedlo/ui'
import { useXField, useXFormState } from '@speedlo/xform'
import { debounce } from 'debounce'
import { autorun } from 'mobx'
import React from 'react'

import {
  QOrderFormAgreementsVariables,
  useQOrderFormAgreements,
} from '../../graph/generated'
import { useOrderModel } from '../../hooks/useOrderModel'
import { OrderAgreementBox } from './OrderAgreementBox'
import { OrderFormValues, TAgreement } from './OrderFormSetup'

const EMPTY_ARRAY = []

export const OrderAgreements: React.FC = () => {
  const orderModel = useOrderModel()

  const formState = useXFormState<OrderFormValues>()
  const [variables, setVariables] = React.useState<
    QOrderFormAgreementsVariables
  >()

  React.useEffect(() => {
    const updateVariablesLater = debounce(setVariables, 2000)

    function extractVariables() {
      const { firstname, lastname, phonePrefix, phone } = formState.values
      return {
        input: {
          orderId: orderModel.orderId!,
          firstname,
          lastname,
          phone: `${phonePrefix.trim()}${phone.trim()}`,
        },
      }
    }

    const dispose = autorun(() => {
      updateVariablesLater(extractVariables())
    })

    return () => {
      dispose()
      updateVariablesLater.clear()
    }
  }, [formState, orderModel])

  const { data, errors, loading } = useQOrderFormAgreements(variables, {
    skip: !variables,
  })

  // remember latest agreements to prevent rendering to blank on update of form values
  const agreements = React.useRef<RoA<TAgreement>>(EMPTY_ARRAY)
  const hasFailed = errors && errors.length > 0

  if (!(loading || hasFailed)) {
    agreements.current = data.agreements
  }

  React.useEffect(() => {
    if (agreements.current.length > 0 && !orderModel.isVamInitialized) {
      orderModel.initVam()
    }
  })

  const { state, renderVamAgreements } = useVamAgreements(agreements.current)

  const validate = React.useCallback(() => {
    const result = agreements.current.some(agreement => {
      return (
        agreement.isMandatory && !state.confirmedAgreements.has(agreement.id)
      )
    })
    return result ? 'mandatory' : undefined
  }, [state])

  const field = useXField('agreements', { validate })

  useAutorun(() => {
    // confirmed agreements are stored in field to be collected for a submission later
    const confirmed = Array.from(state.confirmedAgreements)
    field.setValue(confirmed)
  })

  if (hasFailed) {
    return (
      <Trans>
        Failed retrieving company agreements. Please try again later or contact
        us.
      </Trans>
    )
  }

  return renderVamAgreements({ AgreementBox: OrderAgreementBox })
}
