import { Marker } from '@react-google-maps/api'
import { useReaction, useWhen } from '@speedlo/hooks'
import { observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components/macro'

import { useRoot } from '../../app/useRoot'
import { GeoPoint, latLngToGps } from '../../helpers/gps'
import { useLocationResolver } from '../../hooks/useLocationResolver'
import { useFoodMenuQuery } from '../foodMenu/hooks/useFoodMenuQuery'
import { AddressSubmit } from './AddressSubmit'
import { DeliveryAddressSearch } from './DeliveryAddressSearch'
import { useDeliveryMapPanning } from './hooks/useDeliveryMapPanning'
import { useOrderCreate } from './hooks/useOrderCreate'

interface IProps {
  onConfirm(): void
}

const StyledDeliveryMap = styled.div`
  .delivery__map__address {
    position: absolute;
    top: 0;
    left: 2.3rem;
    right: 2.3rem;
  }
  .delivery__map__submit {
    position: absolute;
    bottom: 3rem;
    width: 100%;
  }
`

export const DeliveryMap = observer<IProps>(({ onConfirm }) => {
  const { map, location, business } = useRoot()

  const resolver = useLocationResolver()

  const resolveUserLocation = (gps: GeoPoint) => {
    location.resetResolution()
    location.setSearchValue('')
    location.setLocation(gps)
  }

  useWhen(
    () => business.isValid,
    () => {
      if (location.hasLocation && !location.hasResolutionResult) {
        location.log('resolving initial gps')
        resolver(location.geoPoint!)
      }
    },
  )

  useReaction(
    () => location.geoPoint,
    gps => {
      if (gps) {
        location.log('resolving on gps change')
        resolver(gps)
      }
    },
  )

  useReaction(
    () => map.clickPoint,
    clickPoint => {
      if (clickPoint) {
        location.log(`resolve with clickpoint`)
        resolveUserLocation(clickPoint)
        map.clearClickPoint()
      }
    },
  )

  // prefetch food menu when correct address is resolved
  useFoodMenuQuery(location.isDeliveryAddress ? location.branchId : null)
  useDeliveryMapPanning()

  const { createDeliveryOrder } = useOrderCreate(onConfirm)

  return (
    <StyledDeliveryMap data-testid="DeliverMap">
      {location.latLng ? (
        <Marker
          position={location.latLng}
          draggable={true}
          onDragEnd={({ latLng }) => {
            location.log(`resolve with drag`)
            resolveUserLocation(latLngToGps(latLng))
          }}
        />
      ) : null}
      <div className="delivery__map__address">
        <DeliveryAddressSearch />
      </div>
      <div className="delivery__map__submit">
        <AddressSubmit
          enabled={location.isDeliveryAddress && !location.isResolving}
          onClick={createDeliveryOrder}
        />
      </div>
    </StyledDeliveryMap>
  )
})
