import React from 'react'
import { ButtonProps } from 'reactstrap'
import styled from 'styled-components/macro'

import { PickerButton } from '../../common/PickerButton'
import { formatTime } from '../../helpers/formats'

interface IProps extends ButtonProps {
  time: Date
  isSelected: boolean
  canBeSelected: boolean
}

const Time = styled.div`
  font-size: 1.2rem;
`

export const TimePickerButton: React.FC<IProps> = ({
  time,
  ...buttonProps
}) => (
  <PickerButton
    size="xs"
    outline={true}
    width="10rem"
    data-testid={`timePickerButton__${time.getTime()}`}
    {...buttonProps}
  >
    <Time>{formatTime(time)}</Time>
  </PickerButton>
)
