import React from 'react'
import { Button, ButtonProps } from 'reactstrap'
import styled from 'styled-components/macro'

interface IProps extends ButtonProps {
  isSelected: boolean
  width: string
}

const PickerButtonStyled = styled(({ isSelected, width, ...props }) => (
  <Button {...props} />
))`
  border-radius: 0.6rem;
  margin: 0.5rem;
  background-color: ${props =>
    props.isSelected ? props.theme.tertiary : 'transparent'} !important;
  color: ${props =>
    props.isSelected ? props.theme.tertiaryText : 'initial'} !important;
  &:hover {
    border-color: ${({ theme }) => theme.tertiaryHover} !important;
  }
  width: ${props => props.width};
  @media (max-width: 370px) {
    width: 100%;
  }
  &.btn-outline-secondary {
    color: #2e2e2e;
    border-color: #d4d4d4;
  }
  &.btn-outline-secondary.active {
    color: white;
  }
`

export const PickerButton: React.FC<IProps> = ({
  isSelected,
  onSelect,
  children,
  ...buttonProps
}) => {
  return (
    <PickerButtonStyled isSelected={isSelected} {...buttonProps}>
      {children}
    </PickerButtonStyled>
  )
}
