import { useApolloClient } from '@speedlo/graphql'

import { useRoot } from '../app/useRoot'
import {
  FCartGoodsItems,
  FCartPresencePresence,
  FRecipeFragment,
  FRecipeFragmentDoc,
  MAddToCartMutation,
  MAddToCartVariables,
  QCartDocument,
  QCartQuery,
  QCartVariables,
  useMAddToCart,
} from '../graph/generated'

let counter = 1

const NO_SIDEDISHES = []

export function useAddToCart() {
  const { order, cart: cartModel } = useRoot()
  const client = useApolloClient()

  const [addToCart] = useMAddToCart({
    context: { noBatch: true },
  })

  const executeAddToCart = (
    recipeId: ID,
    sideDishes: RoA<ID> = NO_SIDEDISHES,
  ) => {
    const variables: MAddToCartVariables = {
      orderId: order.orderId!,
      recipeId,
      sideDishes,
      withGoods: cartModel.withGoods,
      withPresence: cartModel.withPresence,
    }
    return addToCart(variables, {
      optimisticResponse: makeOptimisticResponse(variables),
    })
  }

  return executeAddToCart

  function makeOptimisticResponse(
    variables: MAddToCartVariables,
  ): MAddToCartMutation | undefined {
    const current = client.readQuery<QCartQuery, QCartVariables>(
      {
        query: QCartDocument,
        variables: {
          orderId: variables.orderId,
          withGoods: cartModel.withGoods,
          withPresence: cartModel.withPresence,
        },
      },
      true,
    )
    if (!current) {
      return undefined
    }
    const id = `-${counter++}`
    const addPresence = (): RoA<FCartPresencePresence> => {
      if (!cartModel.withPresence) {
        return current.order.presence
      }
      return [
        ...current.order.presence,
        {
          __typename: 'OrderRecipe',
          id,
          recipe: {
            __typename: 'Recipe',
            id: variables.recipeId,
          },
        },
      ]
    }
    const addItem = (): RoA<FCartGoodsItems> => {
      if (!cartModel.withGoods) {
        return current.order.items
      }
      const recipe = client.readFragment<FRecipeFragment>(
        {
          id: `Recipe-${variables.recipeId}`,
          fragment: FRecipeFragmentDoc,
          fragmentName: 'FRecipe',
        },
        true,
      )
      if (!recipe) {
        return current.order.items
      }
      return [
        ...current.order.items,
        {
          __typename: 'OrderRecipe',
          automatic: false,
          id,
          price: {
            __typename: 'PriceConverted',
            formattedValue: '???',
            value: -1,
          },
          recipe,
          sideDishes: [],
        },
      ]
    }
    return {
      __typename: 'Mutation',
      add: {
        ...current.order,
        countWares: current.order.countWares + 1,
        totalSum: {
          ...current.order.totalSum,
          value: -1,
        },
        items: addItem(),
        presence: addPresence(),
      },
    }
  }
}
