import { useRouterContext } from '@speedlo/hooks'
import { useGoPay } from '@speedlo/payments'
import React from 'react'

import { useRoot } from '../../../app/useRoot'
import { useExecutableQOrderPaymentStatus } from '../../../graph/generated'
import { useAlertModal } from '../../../hooks/useAlertModal'
import { i18n } from '../../../i18n'

export function useOrderPayment() {
  const { order: orderModel, business } = useRoot()
  const { history } = useRouterContext()

  const [paymentErrorModal, alertPaymentError] = useAlertModal()

  const [executePaymentStatusCheck] = useExecutableQOrderPaymentStatus({
    fetchPolicy: 'no-cache',
  })

  const onFinish = React.useCallback(() => {
    history.replace('/finish')
  }, [history])

  const onError = React.useCallback(
    errors => {
      const phone = business.branch!.phone
      alertPaymentError(
        i18n.t`Payment status check has failed`,
        errors.length
          ? errors.join('<br />')
          : i18n.t`We were unable to verify status of your payment. Please contact operator using number ${phone}`,
      )
    },
    [alertPaymentError, business],
  )

  const queryPaymentStatus = React.useCallback(
    (orderId = orderModel.orderId!, userToken) => {
      if (!orderId) {
        return Promise.resolve(null)
      }
      orderModel.goPayStatusCheck(true)
      return executePaymentStatusCheck(
        { orderId },
        {
          fetchPolicy: 'no-cache',
          context: {
            invalidateWithToken: userToken,
            noBatch: true,
          },
        },
      )
        .then(result => {
          return result ? result.status : null
        })
        .finally(() => {
          orderModel.goPayStatusCheck(false)
          return null
        })
    },
    [executePaymentStatusCheck, orderModel],
  )

  const { executeGoPayPayment, paymentLoading: paymentProcessing } = useGoPay(
    queryPaymentStatus,
    {
      onError,
      onFinish,
    },
  )

  return { paymentErrorModal, executeGoPayPayment, paymentProcessing }
}
