import { useXFormCtx } from '@speedlo/xform'
import { observer } from 'mobx-react-lite'
import React from 'react'
import Spinner from 'react-spinkit'

import { PrimaryButton } from '../../common/Buttons'
import { useOrderModel } from '../../hooks/useOrderModel'
import { i18n } from '../../i18n'
import { useOrderFormCheck } from './hooks/useOrderFormCheck'

interface IProps {
  children: ReactNode
}

export const OrderSubmitButton = observer<IProps>(({ children }) => {
  const xform = useXFormCtx()
  const orderModel = useOrderModel()

  const { hasItemsInCart } = useOrderFormCheck()

  let submitBlocked = true
  let submitBlockReason = ''

  if (!hasItemsInCart) {
    submitBlockReason = i18n.t`Cart is empty`
  } else if (!orderModel.isVamInitialized) {
    submitBlockReason = i18n.t`Agreements are not initialized yet`
  } else if (!xform.state.isValid) {
    submitBlockReason = i18n.t`Form validation failed`
  } else {
    submitBlocked = false
  }

  return (
    <>
      <PrimaryButton
        id="OrderSubmitButton"
        title={submitBlockReason}
        type="submit"
        color="primary"
        size="md"
        disabled={submitBlocked || xform.state.isSubmitting}
      >
        {xform.state.isSubmitting ? (
          <Spinner name="circle" fadeIn="none" color="#ccc" />
        ) : (
          children
        )}
      </PrimaryButton>
    </>
  )
})
