import '../../common/Modal.css'

import { Trans } from '@lingui/macro'
import { RM } from '@speedlo/tools'
import React from 'react'
import { Container, Modal, ModalBody, ModalHeader } from 'reactstrap'

import Pacman from '../../common/Pacman'
import { FRecipeDetailFragment } from '../../graph/generated'
import { SidedishPicker } from '../sidedishPicker/SidedishPicker'
import { CartOrderButton } from './CartOrderButton'

interface IProps {
  recipe: Maybe<FRecipeDetailFragment>
  isLoading: boolean
  initialSideDishes?: ID[]
  onConfirm(selection: ID[]): Promise<boolean>
  onClose(): void
}

const EMPTY_SELECTION = []

export const CartModal: React.FC<IProps> = ({
  recipe,
  isLoading,
  initialSideDishes = EMPTY_SELECTION,
  onConfirm,
  onClose,
}) => {
  const [selection, updateSelection] = React.useState([] as string[])

  React.useEffect(() => {
    updateSelection(initialSideDishes)
  }, [initialSideDishes])

  const onCloseModal = React.useCallback(() => {
    updateSelection([])
    onClose()
  }, [onClose])

  const onConfirmAndClose = async () => {
    if (await onConfirm(selection)) {
      onCloseModal()
      return true
    }
    return false
  }

  const onAddSidedish = (sidedishId: ID) => {
    updateSelection(sel => {
      return RM.append(sidedishId, sel)
    })
  }

  const onRemoveSidedish = (sidedishId: ID) => {
    updateSelection(sel => {
      return RM.remove(RM.indexOf(sidedishId, sel), 1, sel)
    })
  }

  const renderBody = () => {
    if (isLoading) {
      return <Pacman />
    }
    if (!recipe) {
      return (
        <Trans>We sorry, but the item is unavailable at this moment.</Trans>
      )
    }
    return (
      <>
        <h3 className="modal__body__food__name">{recipe.nameLabel}</h3>
        <span className="modal__body__food_description">
          {recipe.descriptionLabel}
        </span>
        <SidedishPicker
          recipe={recipe}
          selection={selection}
          onAdd={onAddSidedish}
          onRemove={onRemoveSidedish}
        />
      </>
    )
  }

  return (
    <Modal
      isOpen={recipe !== null}
      toggle={onCloseModal}
      className="modal__container"
      autoFocus={true}
      backdrop="static"
      fade={false}
      data-testid="CartModal"
    >
      <ModalHeader
        toggle={onCloseModal}
        tag={Container}
        className="modal__header"
      >
        <span>
          <Trans>Do you want to add something?</Trans>
        </span>
      </ModalHeader>
      <ModalBody className="modal__body">
        <div className="modal__body__content">{renderBody()}</div>
        <div className="modal__body__button__line">
          <div className="modal__body__button__strip" />
          <CartOrderButton
            recipe={recipe}
            selection={selection}
            onConfirm={onConfirmAndClose}
          />
        </div>
      </ModalBody>
    </Modal>
  )
}
