import '../../common/Modal.css'

import { Trans } from '@lingui/macro'
import { observer } from 'mobx-react-lite'
import React from 'react'
import { Container, Modal, ModalBody, ModalHeader } from 'reactstrap'

import { useRoot } from '../../app/useRoot'
import { PrimaryButton } from '../../common/Buttons'
import { useDelayedOrder } from '../../hooks/useDelayedOrder'
import { i18n } from '../../i18n'
import { TimePicker } from '../timePicker/TimePicker'

export const OrderTimePicker = observer(() => {
  const { order } = useRoot()

  const { getSteps, canBeDelayed, changeDeliveryTime } = useDelayedOrder()

  const selectedTime = React.useRef<Maybe<Date>>(order.deliverAt)
  const onStepChange = React.useCallback((step: Date) => {
    selectedTime.current = step
  }, [])

  const onConfirmAndClose = () => {
    const time = selectedTime.current
    changeDeliveryTime(time)
    order.closeTimePicker()
  }

  const renderModalContent = () => {
    if (!canBeDelayed()) {
      return (
        <Trans>
          It's not possible to pick a specific order time at this moment.
        </Trans>
      )
    }

    return (
      <TimePicker
        steps={getSteps()!}
        initialTime={order.deliverAt}
        onStepChange={onStepChange}
      />
    )
  }

  const renderBottomLine = () => {
    if (!canBeDelayed()) {
      return null
    }
    return (
      <>
        <div className="modal__body__button__strip" />
        <PrimaryButton
          size="md"
          onClick={onConfirmAndClose}
          className="modal__body__button"
        >
          <Trans>Confirm time</Trans>
        </PrimaryButton>
      </>
    )
  }

  const header = order.usingDeliveryOrderMethod
    ? i18n.t`Deliver at what time?`
    : i18n.t`When you will pickup the order?`

  return (
    <Modal
      isOpen={order.timePickerOpen}
      toggle={order.closeTimePicker}
      className="modal__container"
      autoFocus={true}
      backdrop="static"
      fade={false}
    >
      <ModalHeader
        toggle={order.closeTimePicker}
        tag={Container}
        className="modal__header"
      >
        <span>{header}</span>
      </ModalHeader>
      <ModalBody className="modal__body">
        <div className="modal__body__content">{renderModalContent()}</div>
        <div className="modal__body__button__line">{renderBottomLine()}</div>
      </ModalBody>
    </Modal>
  )
})
