import { useSideDishValidator } from '@speedlo/cart'
import React, { Fragment } from 'react'
import styled from 'styled-components'

import { FRecipeDetailFragment } from '../../graph/generated'
import { CategoryValidationMessage } from './CategoryValidationMessage'
import { SidedishCheckbox } from './SidedishCheckbox'

interface IProps {
  recipe: FRecipeDetailFragment
  selection: ReadonlyArray<ID>
  onAdd(item: ID): void
  onRemove(item: ID): void
}

const StyledSidedishPicker = styled.div`
  margin-top: 1rem;

  & .sidedish__picker__checkbox {
    @media (max-width: 370px) {
      width: 100%;
    }
  }

  & .sidedish__picker__category {
    font-size: 1rem;
    font-weight: bold;
    text-align: left;
    margin: 0 0 0.2rem 0.2rem;
  }
`

export const SidedishPicker: React.FC<IProps> = ({
  recipe,
  selection,
  onAdd,
  onRemove,
}) => {
  const { canBeSelected, isCategoryValid } = useSideDishValidator(
    recipe.categories,
    selection,
  )

  return (
    <StyledSidedishPicker className="food__sidedish__picker">
      {recipe.categories.map(category => (
        <Fragment key={category.id}>
          <div className="sidedish__picker__category">
            {category.nameLabel}{' '}
            <CategoryValidationMessage
              minCount={category.minCount}
              maxCount={category.maxCount}
              isValid={isCategoryValid(category)}
            />
          </div>
          {category.sideDishes.map(sidedish => (
            <SidedishCheckbox
              key={`${sidedish.id}-${category.id}`}
              sidedish={sidedish}
              count={selection.filter(i => i === sidedish.id).length}
              isSelected={selection.includes(sidedish.id)}
              canBeRemoved={selection.includes(sidedish.id)}
              canBeSelected={canBeSelected(category, sidedish)}
              onAdd={() => onAdd(sidedish.id)}
              onRemove={() => onRemove(sidedish.id)}
              className="sidedish__picker__checkbox"
            />
          ))}
        </Fragment>
      ))}
    </StyledSidedishPicker>
  )
}
