import { useXFieldForInput } from '@speedlo/xform'
import { observer } from 'mobx-react-lite'
import React from 'react'
import { FormFeedback, Input, Label } from 'reactstrap'

import { useOrderModel } from '../hooks/useOrderModel'

interface IProps extends React.InputHTMLAttributes<HTMLInputElement> {
  name: string
  label?: string
  errorForceDisplay?: boolean
}

export const hiddenError = '<hidden>'

export const FormTextInput = observer<IProps>(
  ({ name, label, errorForceDisplay = false, ...element }) => {
    const orderModel = useOrderModel()
    const [field, getFieldProps] = useXFieldForInput(name)

    const labelNode = (
      <Label for={name} hidden={!label}>
        {element.placeholder || label}
      </Label>
    )

    const showError = errorForceDisplay || field.touched

    return (
      <>
        {labelNode}
        <Input
          {...getFieldProps()}
          {...element}
          type="text"
          valid={field.touched && !field.error}
          invalid={showError && Boolean(field.error)}
          disabled={orderModel.disableInteraction}
        />
        {showError && field.error !== hiddenError ? (
          <FormFeedback valid={false} tooltip={false}>
            {field.error}
          </FormFeedback>
        ) : null}
      </>
    )
  },
)
