import { t } from '@lingui/macro'
import React, { CSSProperties } from 'react'

import { i18n } from '../../i18n'

interface IProps {
  minCount: number
  maxCount: number
  isValid: boolean
}

export const CategoryValidationMessage: React.FC<IProps> = ({
  minCount,
  maxCount,
  isValid,
}) => {
  let message = ''
  if (minCount === 0) {
    if (maxCount > 0) {
      message = i18n._(t`maximum ${maxCount}`)
    } else {
      return null
    }
  } else if (maxCount === 0) {
    message = i18n._(t`minimum ${minCount}`)
  } else if (minCount === maxCount) {
    const count = minCount
    message = i18n._(t`only ${count}`)
  } else {
    message = i18n._(t`minimum ${minCount} and maximum ${maxCount}`)
  }
  const style: CSSProperties = {}
  if (!isValid) {
    style.color = 'red'
  }
  return (
    <span style={style}>
      {'('}
      {message}
      {')'}
    </span>
  )
}
