import { observer } from 'mobx-react-lite'
import React from 'react'

import { useQCartRecipe } from '../../graph/generated'
import { useAddToCart } from '../../hooks/useAddToCart'
import { useOrderModel } from '../../hooks/useOrderModel'
import { CartModal } from './CartModal'

interface IProps {
  orderId: MaybeID
}

export const AddToCartModal = observer<IProps>(({ orderId }) => {
  const orderModel = useOrderModel()

  const recipeId = orderModel.addToCartRecipeId

  const { data, loading } = useQCartRecipe(
    {
      recipeId: recipeId!,
    },
    { skip: orderId === null || recipeId === null, fetchPolicy: 'cache-first' },
  )

  const executeAddToCart = useAddToCart()

  const onConfirm = React.useCallback(
    async sideDishes => {
      if (orderId === null || recipeId === null) {
        return false
      }
      const result = await executeAddToCart(recipeId, sideDishes)
      return Boolean(result)
    },
    [executeAddToCart, orderId, recipeId],
  )

  const onClose = React.useCallback(() => {
    orderModel.addToCart(null)
  }, [orderModel])

  return (
    <CartModal
      recipe={loading ? null : data.recipe}
      onConfirm={onConfirm}
      onClose={onClose}
      isLoading={loading}
    />
  )
})
