import { IAgreementBoxProps } from '@speedlo/ui'
import { observer } from 'mobx-react-lite'
import React from 'react'
import { Button, Collapse, Input } from 'reactstrap'
import styled from 'styled-components/macro'

import { StyledFormCheckbox } from '../../forms/FormCheckbox'
import { useOrderModel } from '../../hooks/useOrderModel'
import { i18n } from '../../i18n'

const StyledOrderAgreementBox = styled.div`
  button {
    padding: 0 0.5rem;
    font-size: 0.8rem;
    min-width: 5rem;
    color: darkgray;
  }
  div.description {
    font-size: 0.8rem !important;
    color: darkgray;
    margin: 0.5rem 0 0.5rem 0;
    & .row {
      margin: 0;
    }
  }
`

export const OrderAgreementBox = observer<IAgreementBoxProps>(
  ({
    agreement,
    isValid,
    isCollapsed,
    titleNode,
    descriptionNode,
    inputProps,
    buttonProps,
  }) => {
    const orderModel = useOrderModel()
    const id = `agreement-${agreement.id}`

    return (
      <StyledOrderAgreementBox>
        <StyledFormCheckbox>
          <Input
            {...inputProps}
            id={id}
            type="checkbox"
            disabled={orderModel.disableInteraction}
            invalid={!isValid}
            onInvalid={ev => {
              if (!isValid) {
                ;(ev.target as HTMLInputElement).setCustomValidity(
                  i18n.t`Please check this box if you want to proceed.`,
                )
              }
            }}
            onClick={ev => {
              ;(ev.target as HTMLInputElement).setCustomValidity('')
            }}
            data-testid={`OrderAgreement${
              agreement.isMandatory ? 'Mandatory' : ''
            }`}
          />
          <label htmlFor={id}>
            {titleNode}
            {agreement.isMandatory && ' * '}
          </label>
          <Button {...buttonProps} color="link">
            {isCollapsed
              ? i18n.t`More...`.toLocaleLowerCase()
              : i18n.t`Less`.toLocaleLowerCase()}
          </Button>
        </StyledFormCheckbox>
        <Collapse isOpen={!isCollapsed} className="description">
          {descriptionNode}
        </Collapse>
      </StyledOrderAgreementBox>
    )
  },
)
