import { isoDate } from '@speedlo/graphql'
import { useRouterContext } from '@speedlo/hooks'
import { Sentry } from '@speedlo/sentry'
import { createObjectValidation, useXForm, XFormProvider } from '@speedlo/xform'
import React from 'react'
import { Form } from 'reactstrap'

import { hiddenError } from '../../forms/FormTextInput'
import {
  MSubmitOrderVariables,
  PaymentGateEnum,
  QOrderFormAgreementsAgreements,
  useMSubmitOrder,
} from '../../graph/generated'
import { useOrderModel } from '../../hooks/useOrderModel'
import { i18n } from '../../i18n'
import { TOrderModel } from '../../models/OrderModel'
import { useOrderFormCheck } from './hooks/useOrderFormCheck'
import { useOrderPayment } from './hooks/useOrderPayment'

export type TAgreement = QOrderFormAgreementsAgreements

export type OrderFormValues = ReturnType<typeof getInitialValues>

export const NOTE_MAX_LENGTH = 50

export const OrderFormSetup: React.FC = ({ children }) => {
  const orderModel = useOrderModel()
  const { history } = useRouterContext()

  const { executeGoPayPayment, paymentErrorModal } = useOrderPayment()

  const [submitOrder] = useMSubmitOrder()

  const validationSchema = React.useMemo(
    () =>
      createObjectValidation(Yup => ({
        firstname: Yup.string().required(hiddenError),
        lastname: Yup.string().required(hiddenError),
        phonePrefix: Yup.string().required(hiddenError),
        phone: Yup.string()
          .required(hiddenError)
          .matches(/\d+/, i18n.t`Only numbers allowed for phone`),
        email: Yup.string()
          .required(hiddenError)
          .email(i18n.t`Invalid email format`),
        note: Yup.string()
          .notRequired()
          .max(
            NOTE_MAX_LENGTH,
            ({ value }) =>
              i18n.t`Maximum length is ${i18n.plural({
                value: NOTE_MAX_LENGTH,
                other: '# characters',
              })}. Typed ${i18n.plural({
                value: value.length,
                one: '# character',
                other: '# characters',
              })}`,
          ),
      })),
    [],
  )

  const { isOrderInCart } = useOrderFormCheck()

  React.useEffect(() => {
    if (orderModel.hasOrder && !isOrderInCart) {
      history.replace('/')
    }
  })

  const onSubmit = React.useCallback(
    async (values: OrderFormValues) => {
      orderModel.submitStarted()

      const variables = getSubmitVariables(values, orderModel)
      const result = await submitOrder(variables)

      if (!result) {
        orderModel.submitFailed()
        return
      }

      const { userErrors, gopayUrl } = result.submitOrder
      if (userErrors.length) {
        orderModel.submitFailed(userErrors)
        return
      }

      if (orderModel.paymentMethod === PaymentGateEnum.Cash) {
        orderModel.submitFinished()
        history.replace(`/finish`)
        return
      }

      if (orderModel.paymentMethod === PaymentGateEnum.Gopay) {
        if (gopayUrl) {
          executeGoPayPayment(gopayUrl).finally(() => {
            orderModel.submitFinished()
          })
        } else {
          Sentry.withScope(scope => {
            scope.setExtras({ variables })
            Sentry.captureMessage('No gopayUrl received for GoPay payment')
          })
          orderModel.submitFailed()
        }
      } else {
        Sentry.withScope(scope => {
          scope.setExtras({ variables })
          Sentry.captureMessage(
            `Unknown payment mode ${orderModel.paymentMethod}`,
          )
        })
        orderModel.submitFailed()
      }
    },
    [executeGoPayPayment, history, orderModel, submitOrder],
  )

  const xform = useXForm({
    initialValues: getInitialValues(),
    validationSchema,
    onSubmit,
    onFormChanges: console.log,
  })

  React.useEffect(() => {
    orderModel.root.cart.setOrderSubmitting(xform.state.isSubmitting)
  }, [xform.state, orderModel.root.cart])

  return (
    <>
      {paymentErrorModal}
      <XFormProvider value={xform}>
        <Form onSubmit={xform.handlers.handleSubmit}>{children}</Form>
      </XFormProvider>
    </>
  )
}

function getInitialValues() {
  return {
    firstname: '',
    lastname: '',
    email: '',
    phone: '',
    phonePrefix: '',
    note: '',
    agreements: [] as RoA<ID>,
  }
}

function getSubmitVariables(
  values: OrderFormValues,
  orderModel: TOrderModel,
): MSubmitOrderVariables {
  return {
    orderId: orderModel.orderId!,
    customer: {
      firstName: values.firstname,
      lastName: values.lastname,
      emails: [{ email: values.email }],
      phones: [{ phone: `${values.phonePrefix}${values.phone}` }],
    },
    note: values.note,
    tips: orderModel.tips,
    paymentGate: orderModel.paymentMethod as PaymentGateEnum,
    deliverAt: orderModel.deliverAt && isoDate(orderModel.deliverAt),
    agreements: values.agreements,
  }
}
