import { useSideDishValidator } from '@speedlo/cart'
import { cn } from '@speedlo/ui'
import React from 'react'

import { PrimaryButton } from '../../common/Buttons'
import { FRecipeDetailFragment } from '../../graph/generated'
import { i18n } from '../../i18n'

interface IProps {
  selection: ReadonlyArray<ID>
  recipe: Maybe<FRecipeDetailFragment>
  onConfirm(): Promise<boolean>
}

export const CartOrderButton = React.memo<IProps>(
  ({ selection, recipe, onConfirm }) => {
    const [isConfirming, setConfirming] = React.useState(false)
    const { isCategoryValid } = useSideDishValidator(
      recipe ? recipe.categories : [],
      selection,
    )

    return (
      <PrimaryButton
        className={cn('modal__body__button', 'animated', {
          heartBeat: isConfirming,
        })}
        disabled={
          isConfirming || !recipe || !recipe.categories.every(isCategoryValid)
        }
        data-testid="CartOrderButton"
        onClick={() => {
          setConfirming(true)
          onConfirm().catch(() => {
            setConfirming(false)
          })
        }}
      >
        {i18n.plural({
          value: selection.length,
          0: 'Order without sidedish',
          one: 'Order with this sidedish',
          other: 'Order with # sidedishes',
        })}
      </PrimaryButton>
    )
  },
)
