import { Trans } from '@lingui/macro'
import { GoogleMap, useLoadScript } from '@react-google-maps/api'
import { Sentry } from '@speedlo/sentry'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../app/useRoot'
import { appConfig } from '../config'
import { TMapModel } from '../models/MapModel'
import Pacman from './Pacman'

interface IBaseMapProps {
  model: TMapModel
  children?: ReactNode | ReactNode[] | (() => ReactNode | ReactNode[])
  isMobile?: boolean
}

/* global google */

const getMapOptions = (isMobile: boolean): google.maps.MapOptions => {
  return {
    streetViewControl: false,
    mapTypeControl: false,
    fullscreenControl: false,
    clickableIcons: false,
    draggableCursor: 'default',
    minZoom: 10,
    zoomControlOptions: {
      position: isMobile
        ? google.maps.ControlPosition.RIGHT_CENTER
        : google.maps.ControlPosition.RIGHT_TOP,
    },
  }
}

const libraries = ['places']

export const BaseMap = observer<IBaseMapProps>(
  ({ model, isMobile = false, children }) => {
    const { user } = useRoot()

    const { isLoaded, loadError, url: googleMapUrl } = useLoadScript({
      id: 'script-loader',
      version: 'weekly',
      googleMapsApiKey: appConfig.googleMapsApi!,
      libraries,
      language: user.language,
    })

    const renderMap = () => {
      return (
        <GoogleMap
          zoom={model.zoom}
          center={model.center}
          onCenterChanged={model.updatePosition}
          onZoomChanged={model.updateZoom}
          onClick={model.onClick}
          onLoad={model.setMapRef}
          onUnmount={() => model.setMapRef(null)}
          options={getMapOptions(isMobile)}
          mapContainerStyle={{
            borderRadius: '0 0 0.5rem 0.5rem',
            overflow: 'hidden',
            height: isMobile ? '100%' : 'calc(100% - 3.75rem)',
          }}
        >
          {typeof children === 'function' ? children() : children}
        </GoogleMap>
      )
    }

    const googleAvailable = Reflect.has(window, 'google')

    if (loadError || (isLoaded && !googleAvailable)) {
      if (loadError) {
        Sentry.withScope(scope => {
          scope.setExtras({ googleMapUrl })
          Sentry.captureException(loadError)
        })
      }
      return (
        <h2>
          <Trans>Cannot load map right now. Please try again later.</Trans>
        </h2>
      )
    }

    return isLoaded ? renderMap() : <Pacman noMargin={true} />
  },
)
