import React from 'react'

import { useRoot } from '../../app/useRoot'
import Pacman from '../../common/Pacman'
import { QOrderFinishQuery, useQOrderFinish } from '../../graph/generated'
import { useEmptyOrderRedirect } from '../../hooks/useEmptyOrderRedirect'

interface IProps {
  render(result: QOrderFinishQuery): ReactNode
}

export const FinishViewLoader: React.FC<IProps> = ({ render }) => {
  const { order } = useRoot()
  useEmptyOrderRedirect()

  const { data, loading } = useQOrderFinish({
    branchId: order.branchId!,
    orderId: order.orderId!,
  })

  if (loading) {
    return <Pacman />
  }

  return render(data)
}
