import { Trans } from '@lingui/macro'
import { useReaction } from '@speedlo/hooks'
import { cn } from '@speedlo/ui'
import { observer, useLocalStore } from 'mobx-react-lite'
import React from 'react'
import { Button } from 'reactstrap'
import styled from 'styled-components'

import { useRoot } from '../../app/useRoot'
import { useAddressSearch } from './hooks/useAddressSearch'

interface IProps {}

const Styled = styled.div`
  position: absolute;
  margin-top: 0.1rem;

  > div,
  button.hint:hover {
    background-color: #ffffffaa;
  }

  > div {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    border: 1px solid transparent;
    border-radius: 0.4rem;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    line-height: 1.5;
    max-width: 25rem;
    text-align: left;

    & > p {
      font-size: 0.8rem;
      color: gray;
    }

    button {
      &:hover,
      &:hover:focus {
        text-decoration: none;
      }
    }

    button.done {
      color: darkgray;
    }
  }

  button.hint {
    color: coral;
    text-shadow: 0.5px 0.5px darkgray;
    &:hover {
      text-decoration: none;
      transition: background-color 0.5s ease;
    }
  }
`

export const ExtraAddressResult = observer<IProps>(props => {
  const { location } = useRoot()
  const state = useLocalStore(() => ({
    open: false,
  }))

  const { results, searching, onSearch } = useAddressSearch({
    count: 5,
    watchLocation: state.open,
  })

  useReaction(
    () => state.open,
    open => {
      if (open) {
        onSearch()
      }
    },
  )

  useReaction(
    () => location.searchValue,
    () => {
      state.open = false
    },
  )

  if (!location.hasSearchValue) {
    return null
  }

  const usefulResults =
    results && results.filter(({ address }) => address !== location.address)

  const renderHint = () => (
    <Button
      className="hint"
      color="link"
      onClick={() => {
        state.open = true
      }}
    >
      <Trans>Looking for a different address?</Trans>
    </Button>
  )

  const renderResults = () => (
    <div>
      <p>
        <Trans>
          Hint: Try moving map around and zooming closer to your location to
          improve these results.
        </Trans>
      </p>
      {searching && <div>...</div>}
      {usefulResults &&
        usefulResults.map(result => (
          <Button
            size="sm"
            color="link"
            key={result.id}
            onClick={() => {
              location.setSearchResult(result)
              state.open = false
            }}
          >
            {result.address}
          </Button>
        ))}
      <Button
        size="sm"
        color="link"
        onClick={() => {
          state.open = false
        }}
        className="done"
      >
        {usefulResults && usefulResults.length === 0 ? (
          <Trans>No match for map location</Trans>
        ) : (
          <Trans>I don't see anything useful here.</Trans>
        )}
      </Button>
    </div>
  )

  return (
    <Styled
      className={cn('animated', {
        bounceInLeft: state.open,
        bounceIn: !state.open,
      })}
      key={state.open.toString()}
    >
      {state.open ? renderResults() : renderHint()}
    </Styled>
  )
})
