import { observer } from 'mobx-react-lite'
import React from 'react'

import { useOrderModel } from '../../hooks/useOrderModel'
import { AddToCartModal } from './AddToCartModal'
import { CartSystem } from './CartSystem'
import { ChangeCartModal } from './ChangeCartModal'

interface IProps {
  isReadonly?: boolean
}

export const CartView = observer<IProps>(({ isReadonly = false }) => {
  const orderModel = useOrderModel()
  const orderId = orderModel.orderId
  return (
    <>
      <AddToCartModal orderId={orderId} />
      <ChangeCartModal orderId={orderId} />
      <CartSystem
        orderId={orderId}
        isReadonly={isReadonly || orderModel.disableInteraction}
      />
    </>
  )
})
