import { useReaction } from '@speedlo/hooks'
import { observer } from 'mobx-react-lite'
import React from 'react'
import FormGroup from 'reactstrap/lib/FormGroup'
import Input from 'reactstrap/lib/Input'
import styled from 'styled-components/macro'

import { useRoot } from '../../app/useRoot'
import { useMOrderTipUpdate } from '../../graph/generated'
import { i18n } from '../../i18n'

const StyledOrderFormTipInput = styled.div`
  margin: 0.8rem 0 1.4rem 0;
`

export const OrderFormTipInput = observer(() => {
  const { order, cart } = useRoot()
  const [updateTip] = useMOrderTipUpdate()

  // TODO: Vyresit aby se nespoustelo po navratu z GoPay
  useReaction(
    () => (order.usingPaymentMethodWithTips ? Number(order.tips) : 0),
    tips => {
      updateTip({
        tips,
        orderId: order.orderId!,
        withGoods: cart.withGoods,
      })
    },
  )

  if (!order.usingPaymentMethodWithTips) {
    return null
  }

  return (
    <StyledOrderFormTipInput>
      <FormGroup>
        <Input
          type="select"
          value={order.tips}
          onChange={ev => {
            order.setTips(Number.parseInt(ev.target.value, 10))
          }}
          disabled={order.disableInteraction}
          data-testid="orderTipsSelect"
        >
          <option value={0}>{i18n.t`Without tips`}</option>
          <option value={5}>{i18n.t`Tips 5%`}</option>
          <option value={10}>{i18n.t`Tips 10%`}</option>
          <option value={15}>{i18n.t`Tips 15%`}</option>
          <option value={20}>{i18n.t`Tips 20%`}</option>
          <option value={25}>{i18n.t`Tips 25%`}</option>
        </Input>
      </FormGroup>
    </StyledOrderFormTipInput>
  )
})
