import { useRouterContext, useWhen } from '@speedlo/hooks'

import { useRoot } from '../app/useRoot'
import { useOrderModel } from './useOrderModel'

export function useEmptyOrderRedirect() {
  const root = useRoot()
  const orderModel = useOrderModel()
  const { history } = useRouterContext()

  useWhen(
    () => root.initialized,
    () => {
      if (!orderModel.hasOrder) {
        history.push('/')
      }
    },
  )
}
