import './CartView.css'

import { Trans } from '@lingui/macro'
import { CartLabels, useCartSystem } from '@speedlo/cart'
import { autorun } from 'mobx'
import { useObserver } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components/macro'

import { useTheme } from '../../app/ThemeSwitchProvider'
import { useRoot } from '../../app/useRoot'
import { PrimaryButton } from '../../common/Buttons'
import { ButtonStrip } from '../../common/ButtonStrip'
import { mobileBreakpoint } from '../../common/Responsive'
import { useQCart } from '../../graph/generated'
import { useOrderModel } from '../../hooks/useOrderModel'
import { useCartControl } from './hooks/useCartControl'

interface IProps {
  orderId: MaybeID
  isReadonly: boolean
  isMobileConfirm?: boolean
}

const StyledCartSystem = styled.div`
  & .animated {
    z-index: 10;
  }
`

export const CartSystem: React.FC<IProps> = ({
  orderId,
  isReadonly,
  isMobileConfirm = false,
}) => {
  const theme = useTheme()
  const labels = React.useMemo(getCartLabels, [])
  const orderModel = useOrderModel()

  const {
    cartModel,
    onItemEdit,
    onItemRemove,
    onPlaceOrder,
    onChangeOrder,
  } = useCartControl()

  const renderMobileSubmit = React.useCallback(() => {
    if (isMobileConfirm && orderModel.disableInteraction) {
      return null
    }
    const shouldPlaceOrder = !isReadonly || orderModel.disableInteraction
    return (
      <ButtonStrip>
        <PrimaryButton
          onClick={shouldPlaceOrder ? onPlaceOrder : onChangeOrder}
        >
          {shouldPlaceOrder ? (
            <Trans>Place order</Trans>
          ) : (
            <Trans>Change order</Trans>
          )}
        </PrimaryButton>
      </ButtonStrip>
    )
  }, [isMobileConfirm, isReadonly, onChangeOrder, onPlaceOrder, orderModel])

  const renderGoodsLoading = React.useCallback(
    () => <Trans>Accessing your order</Trans>,
    [],
  )

  const cart = useCartSystem({
    colors: {
      primary: theme.primary,
      sumRowBg: theme.primary,
      sumRowText: 'white',
      mobileSummaryItemCount: theme.secondary,
    },
    labels,
    isReadonly,
    mobileBreakpoint,
    backgroundImage: theme.backgroundCart,
    onItemEdit,
    onItemRemove,
    renderMobileSubmit,
    renderGoodsLoading,
  })

  React.useEffect(
    () =>
      autorun(() => {
        cartModel.updateDisplay(cart.store.isMobile, cart.store.showGoods)
      }),
    [cart.store, cartModel],
  )

  const root = useRoot()

  return useObserver(function useObserverBody() {
    const { data, loading } = useQCart(
      {
        orderId: orderId!,
        withGoods: cartModel.withGoods,
        withPresence: cartModel.withPresence,
      },
      {
        skip: !root.initialized || !orderId,
        context: {
          noBatch: true,
        },
      },
    )

    React.useEffect(() => {
      if (!loading) {
        cart.updateSummary(data.order)
        if (cartModel.withGoods) {
          cart.updateGoods(data.order)
        }
      }
    })

    return <StyledCartSystem>{cart.renderCart()}</StyledCartSystem>
  })
}

function getCartLabels(): CartLabels {
  return {
    noSideDish: <Trans>Without sidedish</Trans>,
    noCost: <Trans>For free</Trans>,
    cartSumHeader: totalPrice => (
      <Trans>In the cart you have goods for {totalPrice}</Trans>
    ),
    emptyCart: <Trans>Cart is empty</Trans>,
    totalDiscount: <Trans>Total discount</Trans>,
    coverPrice: <Trans>Covers</Trans>,
    transportFee: <Trans>Delivery</Trans>,
    totalSum: <Trans>Total</Trans>,
    tip: <Trans>Tip</Trans>,
    minimalOrderPrice: price => (
      <Trans>Minimal price of order is {price}</Trans>
    ),
  }
}
