import { useGoogleMap } from '@react-google-maps/api'
import React from 'react'

import { useRoot } from '../../../app/useRoot'
import { GeoPoint, gpsToLatLngLit } from '../../../helpers/gps'

export function useMapPanning() {
  const { map } = useRoot()
  const mapRef = useGoogleMap()

  const prevGps = React.useRef<Maybe<GeoPoint>>(null)

  const panToGps = (gps: GeoPoint) => {
    if (!mapRef) {
      map.log('cannot pan map as ref is not available')
      return
    }
    const latLng = gpsToLatLngLit(gps)
    map.log('panning to %j', latLng)
    mapRef.panTo(latLng)
    prevGps.current = gps
  }

  return (gps: GeoPoint) => {
    const prev = prevGps.current
    if (
      !prev ||
      (prev.latitude !== gps.latitude && prev.longitude !== gps.longitude)
    ) {
      panToGps(gps)
    }
  }
}
