import { Trans } from '@lingui/macro'
import React from 'react'
import styled from 'styled-components/macro'

import { useRoot } from '../../app/useRoot'
import Pacman from '../../common/Pacman'
import { mediaDesktop, mediaMobile } from '../../common/Responsive'
import { QFoodMenuWareCategories } from '../../graph/generated'
import { useAddToCart } from '../../hooks/useAddToCart'
import FoodCategory from './FoodCategory'
import FoodMenuItem from './FoodMenuItem'
import { useFoodMenuPresence } from './hooks/useFoodMenuPresence'

interface IProps {
  orderId: MaybeID
  categories: ReadonlyArray<QFoodMenuWareCategories>
  isLoading: boolean
}

const StyledFoodMenu = styled.div`
  border-radius: 0 0 0.6rem 0.6rem;
  ${mediaDesktop`background: white;`};
  ${mediaMobile`
    background: #eee9e7;
    background-size: cover;
    background-attachment: scroll;
  `};

  & > div {
    ${mediaDesktop`
      margin: 0 1.4rem 1rem 1.6rem;
      padding: 2rem 0 2rem 0;
    `};
    ${mediaMobile`
      margin: 0 0.7rem 1rem 0.7rem;
      padding: 0 0 4rem 0;
    `};
  }
`

const FoodMenu: React.FC<IProps> = ({ orderId, categories, isLoading }) => {
  const { order: orderModel, config, user } = useRoot()

  const presence = useFoodMenuPresence(orderId)

  const executeAddToCart = useAddToCart()
  const onAddToCart = React.useCallback(
    (recipeId: ID, hasSideDish: boolean) => {
      if (orderModel.disableInteraction) {
        return
      }
      if (presence.size >= config.orderItemsMax) {
        user.showOrderLimit()
      } else if (hasSideDish) {
        orderModel.addToCart(recipeId)
      } else {
        orderModel.setUsedRecipeId(recipeId)
        executeAddToCart(recipeId)
      }
    },
    [config.orderItemsMax, executeAddToCart, orderModel, presence, user],
  )

  const render = () => {
    if (isLoading) {
      return <Pacman noMargin={true} />
    }
    if (categories.length === 0) {
      return (
        <h3>
          <Trans>Food menu is empty. We are sorry.</Trans>
        </h3>
      )
    }
    return (
      <>
        {categories.map(category => (
          <FoodCategory key={category.id} category={category}>
            {category.recipes.map(recipe => (
              <FoodMenuItem
                key={recipe.id}
                recipe={recipe}
                presence={presence}
                onAddToCart={onAddToCart}
              />
            ))}
          </FoodCategory>
        ))}
      </>
    )
  }

  return (
    <StyledFoodMenu data-testid="FoodMenu">
      <div>{render()}</div>
    </StyledFoodMenu>
  )
}

export default React.memo(FoodMenu)
